@font-face {
  font-family: "Avenir";
  src: url(./fonts/Avenir-Book.ttf) format("truetype");
  font-style: normal;
  font-weight: 300;
}
@font-face {
  font-family: "Avenir";
  src: url(./fonts/Avenir-Heavy.ttf) format("truetype");
  font-style: normal;
  font-weight: 800;
}

html {
  font-family: Avenir, sans-serif;
}

:root {
  --hover-bg-color: 0 3px 8px rgba(61, 78, 97, 0.2);
  --focus-bg-color: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
}

.search {
  font-family: Avenir, Helvetica, sans-serif;
  font-weight: 300;
  background: #fff;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61,78,97,0.1);
  border-radius: 4px;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
  
.big-search {
  padding-left: 62px;
  font-size: 16px;
  line-height: 22px;
}
  
.small-search {
  padding-left: 33px;
  font-size: 14px;
  line-height: 19px;
}
  
.form {
  min-width: 427px;
  margin-top: 20px;
  position: relative;
}
  
.big-form {
  height: 70px;
}
  
.small-form {
  height: 42px;
}
  
.icon1 {
  top: 25px;
  left: 26px;
  position: absolute;
}
  
.icon2 {
  top: 15px;
  left: 13px;
  height: 11px;
  position: absolute;
}
  
.search:hover {
  box-shadow: var(--hover-bg-color);
}
  
.search:focus {
  background: var(--focus-bg-color);
  border-radius: 3px;
  outline: none;
  font-weight: 600;
}
